import { useState } from "react";
import "./Main.css"
import axios from "axios"

const Main = () => {
  const[content, setContent] = useState("Rien")

  const handleClick = async () => {
    await axios.get("http://localhost:8000/getData").then((res) => {
      console.log("RESPONS =>", res);
      setContent(res.data)
    });
  }
	return (
	<div className="main">
    <div className="btn" onClick={handleClick}>GET DATA</div>
    <div className="data">{content}</div>
  </div>
	)
};

export default Main;